<template>
  <div class="article-editor">
    <div class="article-title">{{title}}</div>
    <div>
      <cherry-components
          ref="cherryComponents"
          :height="height"
          :editorModel="editorModel"
      ></cherry-components>
    </div>
  </div>
</template>

<script>
import CherryComponents from "@/components/Cherry/CherryComponents";
import {queryArticleDetail} from "@/api/document/document";


export default {
  name: "DetailIndex",
  components: {
    CherryComponents
  },
  data() {
    return {
      title:null,
      // 编辑模式
      editorModel: 'previewOnly',
      // 自定义编辑器高度
      height: '800px',
      articleId: null
    }
  },
  created() {
    let id = this.$route.query.id;
    if(id){
      this.articleId = id
    }
  },
  mounted() {
    this.queryArticleDetail();
  },
  methods: {
    queryArticleDetail() {
      queryArticleDetail({id:this.articleId}).then(res => {
        console.log(res)
        this.title = res.data.title
        this.$refs.cherryComponents.cherryInstance.setValue(res.data.content);
      })
    }
  }
}
</script>

<style scoped>
.article-editor {
  width: 1000px;
  margin: 0 auto;
  height: 100%;
  border: 1px solid grey;
  padding: 5px;
  border-radius: 5px;
}

.article-title {
  text-align: center;
  height: 40px;
  font-weight: bold;
  padding: 10px;
  font-size: 30px;
}
</style>